@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
    box-sizing: border-box;
}
body {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    background: #EDF7F0;
}

.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align:center;
}

img {
    width: 100px;
}
.roster-container {
    background-color: #DDEBF8;
    border: 2px solid white;
    margin-bottom: 1rem;
    border-radius: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.roster-container > * {
    margin: 0;
    padding: 5px;
    list-style-type: none;
}

.nav-container {
    align-self: end;
    margin-top: 5px;
}

.matchup-container {
    width: 550px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

 .roster-container {
    width: 250px;
    height: auto;
}

.allMatchups-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-container {
    display: flex;
    justify-content: space-between;
}
a {
    font-size:1.1rem;
    text-decoration: none;
    margin: 0 5px;
}

a:hover {
    background-color: rgb(212, 196, 196);
}

.teams-container {
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

@media (max-width: 420px){
    .app-container {
        width: 100vw;
        padding: 0 5px;
    }

   .matchup-container {
    width: auto;
    font-size: .75rem;
    justify-content: normal;
   }

   .roster-container {
       display: flex;
       width: 50%;
       justify-content: flex-start;
   }
   .teams-container .roster-container {
       width: 90%;
   }
   .roster-container ol {
       text-align: left;
   }
    a {
        font-size: .75rem;
    }
}
